<template>
  <v-card elevation="2" outlined>
    <v-card-text>
      <p class='text-h6 text--primary'> Ip Configurations </p>

      <v-container>

        <v-row>
          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> MAC Address </v-col>
          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-start font-weight-bold"> {{MAC}} </v-col>
          <v-col cols="2"></v-col>
          <v-col cols="2"></v-col>
          <v-col cols="2"></v-col>
          <v-col cols="2"></v-col>
        </v-row>

        <v-row>
          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> DHCP State </v-col>
          <v-col cols="2">
            <v-btn-toggle color="primary" mandatory v-model="new_dhcp">
              <v-btn outlined :value="true"> 
                DHCP
              </v-btn>
              <v-btn outlined :value="false">
                Static 
              </v-btn>
            </v-btn-toggle>
            
          </v-col>

          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> Subnet </v-col>
          <v-col cols="2">
            <v-text-field class="text-body-1 font-weight-medium" dense hide-details
              :rules="[IsValidIp(new_netmask)]" :disabled="new_dhcp" v-model="new_netmask"
              type="text" label="Your Subnet"></v-text-field>
          </v-col>
        </v-row>

        <v-row>
          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> Address </v-col>
          <v-col cols="2" align-self="center" >
            <v-text-field class="text-body-1 font-weight-medium" dense hide-details
              :rules="[IsValidIp(new_address)]" :disabled="new_dhcp" v-model="new_address"
              type="text" label="Your IP Address"></v-text-field>
          </v-col>
          
          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> Gateway </v-col>
          <v-col cols="2" align-self="center" >
            <v-text-field class="text-body-1 font-weight-medium" dense hide-details
              :rules="[IsValidIp(new_gateway)]" :disabled="new_dhcp" v-model="new_gateway"
              type="text" label="Your Gateway"></v-text-field>
          </v-col>
        </v-row>

        <v-row>
          <v-col cols="2" class="text-body-1 d-flex justify-end">
            <v-btn class="mx-1" color="primary"
            :disabled="(IsDifferent == false || AllIpIsValid == false)" v-on:click="SetIpInfo">Submit </v-btn>
          </v-col>
        </v-row>

      </v-container>

    </v-card-text>
    
  </v-card>

</template>


<script>
import { mapActions } from 'vuex';

export default {
  name: 'IpConfiguration',

  props: {
    Address: {
      type: String,
      default: "192.168.10.10"
    },
    Subnet: {
      type: String,
      default: "255.255.255.0"
    },
    Gateway: {
      type: String,
      default: "192.168.10.1"
    },
    IsDhcp: {
      type: Boolean,
      default: false
    },
    MAC: {
      type: String,
      default: "48:65:EE:48:01:02"
    }
  },

  data: function() {
    return {
      new_address: this.Address,
      new_netmask: this.Subnet,
      new_gateway: this.Gateway,
      new_dhcp: this.DHCP
    }
  },

  computed: {
    AllIpIsValid() {
      if (this.new_dhcp) {
        return true;
      }
      return this.IsValidIp(this.new_address) 
        && this.IsValidIp(this.new_netmask) 
        && this.IsValidIp(this.new_gateway);
    },

    IsDifferent() {
      var result = this.IsDhcp != this.new_dhcp
        || this.Address != this.new_address
        || this.Subnet != this.new_netmask
        || this.Gateway != this.new_gateway;
      return result;
    }

    
  },

  methods: {
    ...mapActions(["PerformSetIpInfo"]),

    IsValidIp(addr) {
      var ipformat = /^(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$/;
      if (addr.match(ipformat))
      {
        return true;
      }
      return false;
    },

    SetIpInfo() {
      if (this.new_dhcp == true || (this.IsValidIp(this.new_address) && this.IsValidIp(this.new_netmask))) {
        var ipInfo = {
          newAddress: this.new_address,
          newNetmask: this.new_netmask,
          newDhcp: this.new_dhcp
        };
        if (this.new_gateway != null && this.IsValidIp(this.new_gateway)) {
          ipInfo.newGateway = this.new_gateway;
        }
        this.PerformSetIpInfo(ipInfo);
      }
    },

  }

}
</script>