<template>
  <v-card outlined>

    <v-snackbar v-model="enabledSnackbar" timeout="5000">
      Go to Status Page to verify.
    </v-snackbar>
    
    <v-card-text>
      <p class='text-h6 text--primary'> Test Faults </p>

      <v-dialog v-model="dialog" width="600px">
        <template v-slot:activator="{on, attrs}">
          <v-btn class="mx-1" color="primary" v-bind="attrs" v-on="on"> 
            Test Fault
          </v-btn>
        </template>

        <v-card>
          <v-card-title>
            Simulating a Fault Condition
          </v-card-title>
          
          <v-card-text>
            <p class='text-body-1 d-flex align-center'>
              Setting temperature to 110.4 to simulate an over temp condition. Verify on Status page. It resets after 10 seconds. 
            </p>

            <v-btn class="mx-1" color="primary" v-on:click="StartFault"> Confirm </v-btn>
          </v-card-text>
        </v-card>

      </v-dialog>
      
    </v-card-text>

  </v-card>
</template>

<script>
import { mapActions } from 'vuex';

export default {
  name: 'TestFault',

  methods: {
    ...mapActions(["PerformTestFaults", "PerformResetFaults"]),

    StartFault: function() {
      this.enabledSnackbar = true;
      this.dialog = false;
      this.PerformTestFaults();
      this.PerformResetFaults();
    },
  },

  data: function() {
    return {
      dialog: false,
      enabledSnackbar: false
    }
  },

}
</script>