<template>
  <div>
    <v-card elevation="2" outlined>
      <v-card-text>
        <p class="text-h6 text--primary">
          RF Definitions
        </p>
        <v-container>
          <v-row v-for="(row, i) in RowData" :key="i">
            <v-col v-for="(item, index) in row.Column" :key="index">
              <v-row>
                <v-col cols="4" align-self="center" class="text-body-1 d-flex justify-end font-italic"> {{item.name}} </v-col>
                <v-col align-self="center" class="text-body-1 d-flex justify-start font-weight-bold red--text"> 
                  <v-btn-toggle mandatory color="primary">
                    <div v-for="(fault, j) in item.faultOptions" :key="j">
                      <v-btn outlined :value="fault" :disabled="fault != item.faultType" class="primary--text">
                        {{fault}}
                      </v-btn>
                    </div>
                  </v-btn-toggle>
                </v-col>
              </v-row>
            </v-col>
          </v-row>
        </v-container>

      </v-card-text>
    </v-card>

    <v-card elevation="2" outlined>
      <v-card-text>
        <p class="text-h6 text--primary">
          RF Settings
        </p>
        <v-container>
          <v-row>
            <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> Bootup RF </v-col>
            <v-col cols="2" align-self="center" >
              <v-btn-toggle color="primary" mandatory v-model="new_BootupRf">
                <v-btn outlined :value="true">
                  On
                </v-btn>
                <v-btn outlined :value="false">
                  Off 
                </v-btn>
              </v-btn-toggle>
            </v-col>
            <v-col cols="1"></v-col>

            <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> RF Offset </v-col>
            <v-col cols="2" align-self="center" class="text-body-1 font-weight-medium">
              <v-text-field suffix="dB" dense hide-details
                v-model="new_RfOffset"
                type="number" label="RF Offset"
                step="0.1" 
                ></v-text-field>
            </v-col>
            <v-col cols="1"></v-col>
          </v-row>

          <v-row>
            <v-col cols="2" class="text-body-1 d-flex justify-end">
              <v-btn class="mx-1" color="primary"
                :disabled="IsDifferent == false" v-on:click="ChangeRfSettings">Submit </v-btn>
            </v-col>
          </v-row>
        </v-container>
        
      </v-card-text>
    </v-card>
  </div>  
</template>


<script>
import { mapActions } from 'vuex';

const AllRows = [
  {
    Column: [
    {
      name: "Max Power",
      faultType: "Fault",
      faultOptions: ["Fault", "Latched", "None"]
    },
    {
      name: "Reverse Power",
      faultType: "Fault",
      faultOptions: ["Fault", "Latched", "None"]
    }],
  },

  {
    Column: [
    {
      name: "Temperature Fault",
      faultType: "Fault",
      faultOptions: ["Fault", "Latched", "None"]
    },
    {
      name: "Inhibit",
      faultType: "Closed",
      faultOptions: ["Closed", "Open"]
    }]
  }
];

export default {
  name: 'RfSettings',
  
  props: {
    BootupRf: {
      type: Boolean, 
      default: false
    },

    RfOffset: {
      type: Number,
      default: 3.7
    },

    FaultDefinitions: {
      type: Array,
      default: function() {
        return ["Fault", "Latched", "None"];
      } 
    },
    FaultType: {
      type: String,
      default: "Fault"
    }
  },

  created() {
    this.RowData = AllRows;
  },


  computed: {
    IsDifferent() {
      return this.BootupRf != this.new_BootupRf
        || this.RfOffset != this.new_RfOffset;
    }
  },

  methods: {
    ...mapActions(["PerformSetRfSettings"]),

    ChangeRfSettings() {
      var rfSettings = {
        BootupRf: this.new_BootupRf,
        RfOffset: Number.parseFloat(this.new_RfOffset)
      };

      this.PerformSetRfSettings(rfSettings);
    },

  },

  data: function() {
    return {
      new_RfOffset: this.RfOffset,
      new_BootupRf: this.BootupRf
    }
  },


}
</script>

<style scoped>
.input-field {
  padding: 5px;
  margin: 5px;
}
</style>