<template>
  <v-card elevation="2" outlined>
    <v-card-text>
      <p class="text-h6 text--primary">
        Serial Interface
      </p>

      <v-container>
        <v-row>
          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> Interface </v-col>
          <!-- <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-start font-weight-bold"> 
            {{Rs485Selected? "RS-485" : "RS-232"}}
          </v-col> -->
          <v-col cols="2" align-self="center" >
            <v-btn-toggle color="primary" mandatory v-model="new_Rs485Selected" >
              <v-btn outlined :value="false"> 
                RS-232
              </v-btn>
              <v-btn outlined :value="true">
                RS-485 
              </v-btn>
            </v-btn-toggle>
          </v-col>

          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> Baudrate </v-col>
          <!-- <v-col cols="1" align-self="center" class="text-body-1 d-flex justify-start font-weight-bold"> 
            {{SerialBaudrate}}
          </v-col> -->
          <v-col cols="2" align-self="center">
            <v-combobox :items="BaudrateOptions" v-model="new_baudrateSelected" hide-details>
            </v-combobox>
          </v-col>
          <v-col></v-col>
        </v-row>

        <v-row>
          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> Data Bits </v-col>
          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-start font-weight-bold"> 8 </v-col>

          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> Stop Bits </v-col>
          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-start font-weight-bold"> 1 </v-col>
        </v-row>

        <v-row>
          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> Parity </v-col>
          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-start font-weight-bold"> None </v-col>

          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> Flow Control </v-col>
          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-start font-weight-bold"> None </v-col>
        </v-row>

        <v-row>
          <v-col cols="2" class="text-body-1 d-flex justify-end">
            <v-btn class="mx-1" color="primary" 
              :disabled="IsDifferent == false" v-on:click="setSerialInterface">Submit </v-btn>
          </v-col>
        </v-row>

      </v-container>
      
    </v-card-text>
  </v-card>
  
</template>


<script>
import { mapActions } from 'vuex';

export default {
  name: 'SerialInterface',

  props: {
    IsRs485: {
      type: Boolean,
      default: true
    },

    SelectedBaudrate: {
      type: Number,
      default: 115200
    },

    BaudrateOptions: {
      type: Array,
      default: function() {
        return [115200, 38400, 9600];
      }
    }
  },

  data() {
    return {
      new_Rs485Selected: this.IsRs485,
      new_baudrateSelected: this.SelectedBaudrate,
    }
  },

  computed: {
    IsDifferent() {
      var result = this.IsRs485 != this.new_Rs485Selected
        || this.SelectedBaudrate != this.new_baudrateSelected;
      return result;
    }
  },

  methods: {
    ...mapActions(["PerformSetSerialInterface"]),

    setSerialInterface() {
      var baudrateInfo = {
        is_rs485: this.new_Rs485Selected,
        serial_baudrate: this.new_baudrateSelected
      };
      this.PerformSetSerialInterface(baudrateInfo);
    }
  }
}
</script>