<template>
  <v-card elevation="2" outlined>
    <v-data-table :headers="headers" :items="events" :items-per-page="10" class="text-body-1">
      <template v-slot:item.DateTime="{ item }">
        <span> {{formatDateTime(item.DateTime)}} </span>
      </template>

      <template v-slot:item.EventType="{ item }">
        <span :class="[item.EventType == 'Alarm' ? 'red--text' : (item.EventType == 'Warning' ? 'amber--text' : '')]"> {{item.EventType}} </span>
      </template>
    </v-data-table>  
  </v-card>
</template>

<script>

import eventData from '../data/Events.json'

export default {
  name: 'MissionEvent',

  methods: {
    formatDateTime(time) {
      var currentTime = new Date(time);
      return currentTime.toISOString().split('.')[0];
    }
  },

  data () {
    return {
      headers: [
        {
          text: "Date",
          value: "DateTime",
          class: "grey darken-3 white--text text-body-1"
        },
        {
          text: "Event Type",
          value: "EventType",
          class: "grey darken-3 darken-1 white--text text-body-1"
        },
        {
          text: "Description",
          value: "EventDescription",
          class: "grey darken-3 darken-1 white--text text-body-1"
        }
      ],
      events: eventData.Events
    }
  },
}
</script>