            ticks: {
              // beginAtZero: true,
              suggestedMin: 0,
              suggestedMax: 65,
              stepSize: 5
            },
            gridLines: {
              display: true
            },
            scaleLabel: {
              display: true,
              labelString: "dBm"
            },
            position: "left"
          },
          {
            id: "B",
            ticks: {
              // beginAtZero: true,
              suggestedMin: 0,
              suggestedMax: 120,
              stepSize: 10,
            },
            gridLines: {
