            scaleLabel: {
              display: true,
              labelString: "C"
            },
            position: "right"
          }],
          xAxes: [{
            display: true,
            type: 'time',
          }]
        },
        legend: {
          display: true
        },
        responsive: true,
        maintainAspectRatio: false,
        
        // tooltips: {enabled: false},
        // hover: {mode: null},

        plugins: {
          pan: {
            enabled: true,
            mode: 'xy'
          },
          zoom: {
