<template>
  <v-card elevation="2" outlined>
    <v-card-text>
      <p class="text-h6 text--primary">
        Firmware Update
      </p>

      <v-container>
        <v-row align="center" :collapse="true">
          <v-col cols="4">
            <v-file-input label="Firmware File" accept="firmware/mm" browse-text="Select File" class="mx-1"></v-file-input>
          </v-col>
          <v-col cols="4">
            <v-btn class="mx-1" color="primary"> Upload </v-btn>
            <v-btn class="mx-1" color="warning"> Cancel </v-btn>
          </v-col>

          <v-col cols="4">
            <v-progress-linear color="light-green darken-4" class="white--text font-weight-bold" striped
              :rotate="-90" :height="25" :value="50">
              {{percentage}}%
            </v-progress-linear>
          </v-col>

        </v-row>
      </v-container>

    </v-card-text>
  </v-card>

</template>


<script>
export default {
  name: 'UpdateFirmware',

  props: {
    percentage: {
      type: Number,
      default: 50
    }
  }
}
</script>