<template>
  <v-card elevation="2" outlined>
    <v-card-text>
      <p class='text-h6 text--primary'>
        Unit Information
      </p>

      <v-container>
        <v-row>
          <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Model</v-col>
          <v-col cols="3" class='text-body-1 d-flex align-center font-weight-bold'> {{Model}} </v-col>

          <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Firmware</v-col>
          <v-col cols="3" class='text-body-1 d-flex align-center font-weight-bold'> {{Firmware}} </v-col>
        </v-row>

        <v-row>
          <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Serial</v-col>
          <v-col cols="3" class='text-body-1 d-flex align-center font-weight-bold'> {{Serial}} </v-col>
          
          <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Bootloader</v-col>
          <v-col cols="3" class='text-body-1 d-flex align-center font-weight-bold'> {{Bootloader}} </v-col>
        </v-row>

        <v-row>
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Attenuator</v-col>          
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center'>
            <v-text-field type="number" suffix="dB" dense hide-details
              :step="AttenIncrement" :min="MinAtten" :max="MaxAtten"
              :rules="[StepAttenIsValid(new_StepAtten)]"
              :label="StepAttenIncrementString" 
              v-model="new_StepAtten"> </v-text-field> 
          </v-col>
        </v-row>
        
        <v-row>
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Software Mute</v-col>
          <v-col align-self="center" cols="2">
            <v-btn-toggle color="primary" mandatory v-model="new_SwEnable">
              <v-btn outlined :value="true" color="green"> 
                Unmute
                <v-icon dark right color="green"> mdi-checkbox-marked-circle </v-icon>
              </v-btn>
              <v-btn outlined :value="false" color="orange">
                Mute 
                <v-icon dark right color="orange"> mdi-cancel </v-icon>
              </v-btn>
            </v-btn-toggle>
          </v-col>
        </v-row>

        <v-row>
          <v-col cols="2" class="text-body-1 d-flex justify-end">
            <v-btn class="mx-1" color="primary"
            :disabled="IsDifferent == false" v-on:click="SetChanges">Submit </v-btn>
          </v-col>
        </v-row>

      </v-container>
      
    </v-card-text>
  </v-card>
</template>


<script>
import { mapActions } from 'vuex';

export default {
  name: 'UnitInfo',

  props: {
    Model: {
      type: String,
      default: "KAA-400S-A2-210011A"
    },
    Serial: {
      type: String,
      default: "SN123"
    },
    Firmware: {
      type: String,
      default: "20211013BC07"
    },
    Bootloader: {
      type: String,
      default: "0.2.2"
    },

    // TX Attenuator Information
    TxStepAtten: {
      type: Number,
      default: 25.5
    },
    AttenIncrement: {
      type: Number,
      default: 0.25
    },
    MaxAtten: {
      type: Number,
      default: 31.75
    },
    MinAtten: {
      type: Number,
      default: 0
    },

    SwTxEnabled: {
      type: Boolean,
      default: false
    }
  },

  computed: {
    StepAttenIncrementString() {
      return "Step Increment is " + parseFloat(this.AttenIncrement).toFixed(2) + "dB";
    },
    
    IsDifferent() {
      return this.new_StepAtten != this.TxStepAtten
        || this.new_SwEnable != this.SwTxEnabled;
    },


  },

  methods: {
    ...mapActions(["PerformSetStepAtten", "PerformSetSwTxEnabled"]),

    StepAttenIsValid(atten) {
      return atten >= this.MinAtten
        && atten <= this.MaxAtten
        && atten % this.AttenIncrement == 0;
    },

    IncrementStepAtten(atten) {
      return atten + this.AttenIncrement;
    },

    SetChanges() {
      this.PerformSetStepAtten(Number.parseFloat(this.new_StepAtten));
      this.PerformSetSwTxEnabled(this.new_SwEnable);
    },

    Submit() {
      if (this.IsDifferent()) {
        this.SetChanges();
      }
    }

  },

  data: function() {
    return {
      new_StepAtten : this.TxStepAtten,
      new_SwEnable: this.SwTxEnabled
    }
  },

}
</script>

