<template>
  <v-card elevation="2" outlined>
    <v-card-text>
      <p class='text-h6 text--primary'> Clock (NTP) </p>

      <v-container>
        <v-row>
          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> Manual Time </v-col>
          <!-- <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-start font-weight-bold"> 
            {{ManualTimeEnabled ? "Manual" : "NTP Server"}}
          </v-col> -->

          <v-col cols="4" align-self="center">
            <v-btn-toggle color="primary" mandatory v-model="new_ManualDateTimeEnabled" >
              <v-btn outlined :value="false">
                NTP Server
                <v-icon dark right> mdi-server </v-icon>
              </v-btn>
              <v-btn outlined :value="true"> 
                Manual
                <v-icon dark right> mdi-checkbox-marked-circle </v-icon>
              </v-btn>
            </v-btn-toggle>
          </v-col>
        </v-row>

        <v-row v-if="new_ManualDateTimeEnabled == false">
          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> Primary </v-col>
          <!-- <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-start font-weight-bold"> {{PrimaryAddress}} </v-col> -->
          <v-col cols="2" align-self="center">
            <v-text-field class="text-body-1 font-weight-medium" dense hide-details
              :rules="[IsValidIp(new_PrimaryAddress)]" v-model="new_PrimaryAddress"
              type="text" label="Primary Address"></v-text-field>
          </v-col>

          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> Secondary </v-col>
          <!-- <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-start font-weight-bold"> {{SecondaryAddress}} </v-col> -->
          <v-col cols="2" align-self="center">
            <v-text-field class="text-body-1 font-weight-medium" dense hide-details
              :rules="[IsValidIp(new_SecondaryAddress)]" v-model="new_SecondaryAddress"
              type="text" label="Secondary Address"> </v-text-field>
          </v-col>
        </v-row>

        <v-row v-if="new_ManualDateTimeEnabled">
          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic">Entered Time</v-col>
          <!-- <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-start font-weight-bold"> 
            {{ManualDateTime}} 
          </v-col> -->
          <v-col cols="4" align-self="center">
            <input type="datetime-local" class="text-body-1" v-model="new_ManualDateTime" pattern="[0-9]{4}-[0-9]{2}-[0-9]{2}T[0-9]{2}:[0-9]{2}"/> 
            <!-- <v-text-field class="text-body-1 font-weight-medium" dense hide-details
              v-model="new_ManualDateTime" :rules="[IsValidDateTime(new_ManualDateTime)]"
              type="time" label="yyyy-mm-dd HH:mm:ss"></v-text-field> -->
          </v-col>
        </v-row>

        <v-row>
          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> UTC Offset </v-col>
          <!-- <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-start font-weight-bold"> 
            {{UtcOffsetHour}} Hour(s) {{UtcOffsetMinute}} Minute(s)
          </v-col> -->
          <v-col cols="1" align-self="center">
            <v-text-field class="text-body-1 font-weight-medium" dense hide-details 
              v-model="new_UtcOffsetHour" :rules="[IsValidHourInput(new_UtcOffsetHour)]"
              type="number" label="Hour(s)"
              min="0" max="26" pattern="\d+">{{UtcOffsetHour}}</v-text-field>
          </v-col>
          <v-col cols="1" align-self="center">
            <v-text-field class="text-body-1 font-weight-medium" dense hide-details 
              v-model="new_UtcOffsetMinute" :rules="[IsValidMinuteInput(new_UtcOffsetMinute)]"
              type="number" label="Minute(s)"
              min="0" max="59" pattern="\d+">{{UtcOffsetMinute}}</v-text-field>
          </v-col>
          
          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> Update Daily </v-col>
          <!-- <v-col cols="1" align-self="center" class="text-body-1 d-flex justify-start font-weight-bold"> {{UpdateDaily ? "Yes" : "No"}} </v-col> -->
          <v-col cols="3" align-self="center">
            <v-btn-toggle color="primary" mandatory v-model="new_UpdateDaily">
              <v-btn outlined :value="true"> 
                Yes
                <v-icon dark right> mdi-checkbox-marked-circle </v-icon>
              </v-btn>
              <v-btn outlined :value="false">
                No 
                <v-icon dark right> mdi-cancel </v-icon>
              </v-btn>
            </v-btn-toggle>
            
          </v-col>
        </v-row>

        <v-row v-if="new_ManualDateTimeEnabled == false">
          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> Clock Sync </v-col>
          <v-col cols="2" align-self="center" :class="[IsSynced? '' : 'red--text', 'text-body-1', 'd-flex' ,'justify-start', 'font-weight-bold']"> {{IsSynced ? "Synced" : "Not Synced"}} </v-col>
        </v-row>

        <v-row>
          <v-col cols="2" class="text-body-1 d-flex justify-end">
            <v-btn class="font-weight-medium" color="primary"
              :disabled="IsDifferent == false">Submit </v-btn>
          </v-col>
        </v-row>

      </v-container>
      
    </v-card-text>

  </v-card>
</template>

<script>

export default {
  name: 'ClockNtp',

  props: {
    PrimaryAddress: {
      type: String,
      default: "192.168.100.10"
    },
    SecondaryAddress: {
      type: String,
      default: "192.168.200.10"
    },
    ManualTimeEnabled: {
      type: Boolean,
      default: true
    },
    ManualDateTime: {
      type: String,
      default: "2020-01-23T23:57"
    },
    UpdateDaily: {
      type: Boolean,
      default: false
    },
    UtcOffsetHour: {
      type: Number,
      default: 1
    },
    UtcOffsetMinute: {
      type: Number,
      default: 30
    },

    IsSynced: {
      type: Boolean,
      default: true
    }
  },

  data: function() {
    return {
      new_PrimaryAddress: this.PrimaryAddress,
      new_SecondaryAddress: this.SecondaryAddress,
      
      new_ManualDateTimeEnabled: this.ManualTimeEnabled,
      new_ManualDateTime: this.ManualDateTime,
      
      new_UpdateDaily: this.UpdateDaily,
      
      new_UtcOffsetHour: this.UtcOffsetHour,
      new_UtcOffsetMinute: this.UtcOffsetMinute,

    }
  },

  computed: {
    IsDifferent() {
      if (this.new_ManualDateTimeEnabled == false) 
      {
        if (this.IsValidIp(this.new_PrimaryAddress)
          && this.IsValidIp(this.new_SecondaryAddress)
          && this.IsValidHourInput(this.new_UtcOffsetHour)
          && this.IsValidMinuteInput(this.new_UtcOffsetMinute)) 
        {
          return this.ManualTimeEnabled != this.new_ManualDateTimeEnabled
            || this.PrimaryAddress != this.new_PrimaryAddress
            || this.SecondaryAddress != this.new_SecondaryAddress
            || this.UtcOffsetHour != this.new_UtcOffsetHour
            || this.UtcOffsetMinute != this.new_UtcOffsetMinute
            || this.UpdateDaily != this.new_UpdateDaily;
        }    
      }
      else
      {
        if (this.IsValidDateTime(this.new_ManualDateTime)
          && this.IsValidIp(this.new_PrimaryAddress)
          && this.IsValidIp(this.new_SecondaryAddress)
          && this.IsValidHourInput(this.new_UtcOffsetHour)
          && this.IsValidMinuteInput(this.new_UtcOffsetMinute))
        {
          return this.ManualTimeEnabled != this.new_ManualDateTimeEnabled
            || this.ManualDateTime != this.new_ManualDateTime
            || this.PrimaryAddress != this.new_PrimaryAddress
            || this.SecondaryAddress != this.new_SecondaryAddress
            || this.UtcOffsetHour != this.new_UtcOffsetHour
            || this.UtcOffsetMinute != this.new_UtcOffsetMinute
            || this.UpdateDaily != this.new_UpdateDaily;
        }    
      }
      return false;
    }
  },

  methods: {
    IsValidIp(addr) {
      var ipformat = /^(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$/;
      if (addr.match(ipformat))
      {
        return true;
      }
      return false;
    },

    IsValidDateTime(dateTime) {
      return isNaN(Date.parse(dateTime)) == false;
    },

    IsValidHourInput(hours) {
      var hr = Number.parseInt(hours, 10)
      if (isNaN(hr) == false) {
        return hr >= 0 && hr < 27;
      }
      return false;
    },
    IsValidMinuteInput(minutes) {
      var min = Number.parseInt(minutes, 10)
      if (isNaN(min) == false) {
        return  min >= 0 &&  min < 60;
      }
      return false;
    }
  },

  watch: {
    new_ManualDateTime: function() {
      console.log(this.new_ManualDateTime);
    }
  }
}

</script>