<template>
  <v-card elevation="2" outlined>
    <v-card-text>
      <p class='text-h6 text--primary'> Status </p>

      <v-container>
        <v-row>
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Mode</v-col>
          <v-col align-self="center" cols="3"
            :class="[IsFaulted? 'red--text' : (SwTxEnabled == false ? 'amber--text' : 'green--text'), 'text-body-1', 'd-flex align-center', 'font-weight-bold']"> 
            <v-tooltip right :disabled="IsFaulted == false">
              <template v-slot:activator="{on}">
                <div v-on="on"> {{IsFaulted ? "Faulted":  (SwTxEnabled == false ? "Muted" : "Active")}} </div>
              </template>
              <span>{{GetFaultsString}}</span>
            </v-tooltip>
            
          </v-col>
        
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Temperature</v-col>
          <v-col align-self="center" cols="3" class='text-body-1 d-flex align-center font-weight-bold'> {{parseFloat(Temperature).toFixed(1)}} &#176;C </v-col>
        </v-row>

        <v-row>
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>RF Power</v-col>
          <v-col align-self="center" cols="3">
            <v-btn class="text-field text-body-1 d-flex align-center font-weight-bold" outlined v-on:click="ToggleConversion">
              {{GetRfPowerLabel}}
            </v-btn>
            
          </v-col>
          
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>RF Offset</v-col>
          <v-col align-self="center" cols="3" class='text-body-1 d-flex align-center font-weight-bold'> {{parseFloat(RfOffset).toFixed(1)}} dB</v-col>
        </v-row>

        <v-row>
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>TxStepAtten</v-col>
          <v-col align-self="center" cols="3" class='text-body-1 d-flex align-center font-weight-bold'> {{parseFloat(TxStepAtten).toFixed(2)}} dB </v-col>

          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Tx Reference</v-col>
          <v-col align-self="center" cols="3" 
            :class="[TxRefLocked ? 'green--text' : 'red--text', 'text-body-1', 'd-flex', 'align-center', 'font-weight-bold']"> 
            {{TxRefLocked ? "Locked" : "Unlocked"}} 
          </v-col>
        </v-row>

        <v-row>
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Software Mute</v-col>
          <v-col align-self="center" cols="3" 
            :class="[SwTxEnabled ? 'green--text' : 'amber--text', 'text-body-1', 'd-flex', 'align-center', 'font-weight-bold']">
            {{SwTxEnabled ? "Unmuted" : "Muted"}} 
          </v-col>

          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Fan</v-col>
          <v-col align-self="center" cols="3" class='text-body-1 d-flex align-center font-weight-bold'> {{FanSpeed}} RPM </v-col>
        </v-row>
      </v-container>
      
    </v-card-text>
  </v-card>
</template>


<script>

export default {
  name: 'Status',

  props: {
    
    TxPower: {
      type: Number,
      default: 38.5
    },
    SwTxEnabled: {
      type: Boolean,
      default: false
    },
    TxRefLocked: {
      type: Boolean,
      default: true
    },
    TxStepAtten: {
      type: Number,
      default: 25.5
    },

    Temperature: {
      type: Number,
      default: 55.4
    },

    FanSpeed: {
      type: Number,
      default: 3490
    },
    

    RfOffset: {
      type: Number,
      default: 3.7
    },

    Faults: {
      type: Array,
      default: function() {
        return ['Master', 'Drain Voltage', 'Fan Power'];
      }
    }

  },

  computed: {
    GetFaultsString() {
      if (this.Faults != null && this.Faults.length > 0) {
        var faultMsg = "";
        this.Faults.forEach(fault => {
          if (faultMsg.length == 0) {
            faultMsg = fault;
          }
          else {
            faultMsg = faultMsg + " | " + fault;
          }
        });
        
        return faultMsg;
      }
      return "";
    },

    IsFaulted() {
      return this.Faults != null && this.Faults.length > 0;
    },

    GetComputedPower() {
      return this.TxPower + this.RfOffset;
    },

    GetRfPowerLabel() {
      switch (this.conversion) {
        // dBw
        case 1: 
          return parseFloat(this.GetComputedPower - 30.0).toFixed(1) + " dBW";

        // Watt
        case 2:
          return parseFloat(this.dBmToWatt(this.GetComputedPower)).toFixed(4) + " W";

        // dBm
        default:
          return parseFloat(this.GetComputedPower).toFixed(1) + " dBm";
      }
    }
  },

  methods: {
    dBmToWatt(power) {
      return (1 * Math.pow(10, power / 10)) / 1000.0;
    },

    ToggleConversion() {
      this.conversion = (this.conversion + 1) % 3;
    }
  },

  data: function() {
    return {
      conversion: 0
    }
  },
}
</script>

<style scoped>
.text-field{
  border: none !important;
  background: none !important;
  padding: 0px !important;
  color: rgba(0, 0, 0, 0.6) !important;
}
</style>
